import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AddressPage } from '../address/address';
import { WholesaleProvider } from '../../providers/wholesale/wholesale';

/**
 * Generated class for the AddressListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-address-list',
  templateUrl: 'address-list.html',
})
export class AddressListPage {
  addrparams = { id: Math.random() };
  addressList:any;
  constructor(public navCtrl: NavController, public navParams: NavParams, public sortService: WholesaleProvider) {
  }

  ionViewDidLoad() {
    this.getAddr();
  }

  goAddr(){
    // console.log("item="+item);
    this.navCtrl.push(AddressPage);
  }

  getAddr(){
    this.sortService.getAddress(this.addrparams).then(data=>{
      this.addressList=data;
    })
  }

}



