import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AddressListPage } from '../address-list/address-list';

/**
 * Generated class for the FillOrderPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-fill-order',
  templateUrl: 'fill-order.html',
})
export class FillOrderPage {
  addrParams = {
    // regionId1:"",
    // regionId2 :"",
    // regionId3 :"",
    // regionId4 :"",
    // regionId5 :"",
    address: "",
    phone: "",
    linkMan: "",
  };
  constructor(public navCtrl: NavController, public navParams: NavParams) {
    // this.addrParams.address = navParams.get("address")||'';
    // this.addrParams.phone = navParams.get("phone")||'';
    // this.addrParams.linkMan = navParams.get("linkMan")||'';
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad FillOrderPage');
    this.addrParams.linkMan = "梁鸿铮222";
  }
  goAddressList() {
    this.navCtrl.push(AddressListPage)
  }

}



